import axios from 'axios'

export default {
  name: 'projectListList',
  data () {
    return {
      list: [],
      tag: this.Run
    }
  },
  methods: {
    goProjectDetail (projectID, projectName) {
      sessionStorage.setItem('pid', projectID)
      sessionStorage.setItem('pname', projectName)
      this.$router.push('/projectDetail')
    },
    getProjectAll () {
      axios.get(this.url + '/project/all')
        .then(this.getProjectListSucc)
    },
    getProjectManager () {
      axios.get(this.url + '/project/manager/' + localStorage.getItem('id'))
        .then(this.getProjectListSucc)
    },
    getProjectMember () {
      axios.get(this.url + '/project/member/' + localStorage.getItem('id'))
        .then(this.getProjectListSucc)
    },
    getProjectListSucc (res) {
      if (res.status === 200) {
        res = res.data
        let projectList = this.list.concat(res)
        const arr = new Map()
        this.list = projectList.filter((projectList) => !arr.has(projectList.id) && arr.set(projectList.id, 1))
      }
    }
  },
  mounted () {
    let level = parseInt(localStorage.getItem('level'))
    if (level === this.PI) {
      this.getProjectManager()
    } else if (level === this.PR) {
      this.getProjectMember()
      this.getProjectManager()
    } else if (level === this.student) {
      this.getProjectMember()
    } else {
      this.getProjectAll()
    }
  }
}



